<template>
    <section class="bg-white text-white pt-4">         

         <div class="bg-blue_1 pb-12">
            <h2 class="text-3xl py-6 text-white 
            md:text-6xl
            lg:text-4xl">- Verificar nueva cuenta -</h2>            

            <div class="container border-2 rounded border-white p-5 m-auto">              
                <div class="shadow-round bg-dark_blue_1 text-xl px-10 py-8 my-3">
                    <p v-if="(!message)">
                        <i class="fa fa-spinner fa-pulse"></i> Procesando confirmación...
                    </p>
                    <h3 v-if="(!!message)"
                    class="w-full uppercase tracking-wider font-bold text-orange_1 text-2xl pb-2">{{ message }}</h3>     
                    <span v-if="submessage">
                        Debe esperar a que la agencia active la cuenta verificada. Puede contactar con nosotros a través de los números de contactos 
                    </span>                      
                </div>
                <a href="/" class="text-white">Ver nuestras ofertas</a> |
                <a href="/destinos/cuba/hotel/" class="text-white">Hoteles</a>
            </div>
         </div>
         
    </section>
</template>
<script>
// import { ModelSelect } from 'vue-search-select'
// import 'vue-search-select/dist/VueSearchSelect.css'

import ClientService from "@/services/user/client.service";

export default {
    name:'VerifyUser',
    components: {
    },
    data() {
      return {
        message: '',  
        submessage: false
      }
    },    
    created() {
        const token = this.$route.params.token        
        if(token) {           
            this.verifyUser(token)
        }            
    },  
    methods: {
        verifyUser(token) {            
            ClientService.verifyUser(token).then(c => {
                if(c.verified) {
                    this.message = 'Su cuenta ha sido verificada satisfactoriamente';
                    this.submessage = true;
                } else {
                    this.message = 'Ha habido un problema a la hora de verificar su cuenta contactenos.'
                    this.submessage = false;
                }
            })
        }
    }
}
</script>
